import {useEffect, useState} from "react";

const SendingCerts = ({socket}) => {
    const [log, setLog] = useState(null);

    useEffect(() => {
        socket.on('log', (data) => {
           setLog(data);
        });
    });
    return (
        <div>
            <div>Loading...</div>
            <div>
                {log}
            </div>
        </div>
    );
};

export default SendingCerts;
